<!-- 层叠柱状图 -->
<style lang="stylus" scoped>
  .line
    height 1000px
    //background url('../../assets/bg.jpg') no-repeat
    background-size 100% 100%
    .main
      width 100%
      height calc(100% - 100px)
      margin-top -15px
</style>

<template>
  <div class="line">
    <!--<v-header :name="name" :legendArr="legendArr" :myChart="myChart"></v-header>
    <v-filter :myChart="myChart" v-if="myChart._dom"></v-filter>-->
    <div class="main"></div>
  </div>

</template>

<script>
  import echarts from 'echarts'
  import header from '@/views/devices/header'
  import filter from '@/views/devices/filter'

  export default {
    data() {
      return {
        legendArr: [],
        //color: this.$store.state.color,
        color: ['#325B69', '#698570', '#AE5548', '#6D9EA8', '#9CC2B0', '#C98769'],
        myChart: {},
        name: '折线图'
      }
    },
    methods: {
      _init() {
        this.legendArr = this.myChart.getOption().series
        this.legendArr.forEach((data) => {
          data.selected = true;
        })
        this.$root.charts.push(this.myChart)
        window.addEventListener('resize', function() {
          this.myChart.resize()
        }.bind(this))
      }
    },
    components: {
      'v-header': header,
      'v-filter': filter
    },
    mounted() {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = echarts.init(document.querySelector('.line .main'));
      this.myChart.setOption({
        title: {
          text: '设备在线数量统计堆叠图',
          textStyle: {//标题内容的样式
            color: '#000000'//
          }
        },
        textStyle: {
          fontSize: 20 // 用 legend.textStyle.fontSize 更改示例大小
        },
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data:['厂1','厂2','厂3','厂4','厂5']
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: ['11月14日','11月15日','11月16日','11月17日','11月18日','11月19日','11月20日'],

        },
        yAxis: {
          type: 'value',

        },

        series: [

          {
            name:'厂1',
            type:'line',
            stack: '总量',
            data:[300, 132, 101, 134, 90, 230, 210],
          },
          {
            name:'厂2',
            type:'line',
            stack: '总量',
            data:[220, 182, 191, 234, 290, 330, 310]
          },
          {
            name:'厂3',
            type:'line',
            stack: '总量',
            data:[150, 232, 201, 154, 190, 330, 410]
          },
          {
            name:'厂4',
            type:'line',
            stack: '总量',
            data:[320, 332, 301, 334, 390, 330, 320]
          },
          {
            name:'厂5',
            type:'line',
            stack: '总量',
            data:[435, 397, 311, 634, 543, 462,588]
          }
        ]
      });
      this._init()
    }
  }

</script>



