<template lang="html" >
  <div class="deviceshome"  >
    <div class="flex-container column" >
      <div class="item one" @click="clickChart('1')" style="transform: translate(-26.5%,-38%) scale(0.25)">
        <v-word></v-word>
      </div>
      <div class="item two" @click="clickChart('2')" style="transform: translate(-26.5%,-12.5%) scale(0.25)">
        <column></column>
      </div>
      <div class="item three" @click="clickChart('3')" style="transform: translate(-26.5%,12.9%) scale(0.25)">
        <v-line></v-line>
      </div>
      <div class="item four " @click="clickChart('4')" style="transform: translate(-26.5%,38.5%) scale(0.25)">
        <cvenum></cvenum>
      </div>
      <div class="item five " @click="clickChart('5')" style="transform: translate(-0.65%,38.5%) scale(0.25)">
        <v-pie></v-pie>
      </div>
      <div class="item six " @click="clickChart('6')" style="transform: translate(26.8%,38.5%) scale(0.28,0.25)">
        <v-meter></v-meter>
      </div>
      <div class="item seven " @click="clickChart('7')" style="transform: translate(54.3%,38.5%) scale(0.25)">
        <ipgraph></ipgraph>
      </div>

      <div class="item eight " @click="clickChart('8')" style="transform: translate(80%,38.5%) scale(0.25)">
        <pierose></pierose>
      </div>
      <div class="item nine " @click="clickChart('9')" style="transform: translate(80%,12.9%) scale(0.25)">
        <cvesort></cvesort>
      </div>
      <div class="item ten " @click="clickChart('10')" style="transform: translate(80%,-12.5%) scale(0.25)">
        <v-scatter></v-scatter>
      </div>
      <div class="item eleven " @click="clickChart('11')" style="transform: translate(80%,-38%) scale(0.25)">
        <v-radar></v-radar>
      </div>
      <div class="item active " @click="clickChart('12')" style="transform: translate(26.75%,-13%) scale(0.8,0.77)">
        <relation></relation>
      </div>

    </div>
  </div>
</template>


<script>
  console.log("this is echarthome vue")
  import column from '@/views/devices/column.vue'
  import line from '@/views/devices/line.vue'
  import multipleColumn from '@/views/devices/multipleColumn.vue'
  import point from '@/views/devices/point.vue'
  import meter from '@/views/devices/meterr.vue'
  import pie from '@/views/devices/pie.vue'
  import radar from '@/views/devices/radarhome.vue'
  import relation from '@/views/devices/relation.vue'
  import cvenum from '@/views/devices/cvenum.vue'
  import cvesort from '@/views/devices/cvesort.vue'
  import pierose from '@/views/devices/pierose.vue'
  import scatter from '@/views/devices/scatter.vue'
  import graph from '@/views/devices/graph.vue'
  import word from '@/views/devices/word.vue'
  import ipgraph from '@/views/devices/ipgraph.vue'



  export default {
    data() {
      return {
        items: []
      }
    },
    mounted() {
      this._init()
    },
    methods: {
      _resize() {
        this.$root.charts.forEach((myChart) => {
          myChart.resize()
        })
      },
      _init() {
        this.items = document.querySelectorAll('.flex-container .item')
        for (let i = 0; i < this.items.length; i++) {
          this.items[i].dataset.order = i + 1;
        }
      },
      clickChart(clickIndex) {
        let activeItem = document.querySelector('.flex-container .active')
        let activeIndex = activeItem.dataset.order
        let clickItem = this.items[clickIndex - 1]
        if (activeIndex === clickIndex) {
          return
        }
        activeItem.classList.remove('active')
        clickItem.classList.add('active')
        this._setStyle(clickItem, activeItem)
      },
      _setStyle(el1, el2) {
        let transform1 = el1.style.transform
        let transform2 = el2.style.transform
        el1.style.transform = transform2
        el2.style.transform = transform1
      }
    },
    components: {
      column,
      multipleColumn,
      point,
      'v-line': line,
      'v-meter': meter,
      'v-pie': pie,
      'v-radar':radar,
      relation,
      cvenum,
      cvesort,
      pierose,
      'v-scatter': scatter,
      'v-graph': graph,
      'v-word':word,
      ipgraph
    }
  }

</script>

<style lang="stylus" scoped>
  *
    box-sizing: border-box;
  .point,.multipleColumn,.columnChart,.line
    height 100%!important
    width 100%!important
    background none!important
  .item
    padding: 0px;
    margin: 0px;
    width: 64%;
    height: 100%;
    position absolute;
    //transform scale(0.33)
    text-align: center;
    transition:all 0.8s;
    background rgba(32, 32, 35, 0.5)


  .flex-container.column
    width: 100%;
    height: 100%;
    box-sizing: content-box;
    margin:  0 auto 0px -87px;
    display :block;
    overflow: hidden;
    background url('/src/assets/images/bg.jpg');
    background-size 100% 100%


  .deviceshome
    //position relative

    margin:0px;
    padding:0px;





  //padding-top 5%
  //background url('../../assets/bg.jpg');
  //background-size 100% 100%
  /* .flex-container.column
     position relative
     height: 90%;
     width: 90%;
     overflow: hidden;
     margin:  0 auto 100px auto;
     box-sizing: content-box;*/
  /*.active
    height 100%
    width: 69%;
    margin-left: 10px;
    line-height: 300px;*/
</style>



