<!-- 柱状图 -->
<style lang="stylus" scoped>
  .derodar
    height 250px
    //background url('../../assets/bg.jpg') no-repeat
    background-size 100% 100%
    color white
    .main
      width 100%
      margin-top 10px
</style>

<template>
  <div class="derodar">
    <!--<v-header :name="name" :legendArr="legendArr" :myChart="myChart"></v-header>
    <v-filter :myChart="myChart" v-if="myChart._dom"></v-filter>-->
    <div class="main"></div>
  </div>

</template>

<script>
  import echarts from 'echarts'
  import header from '@/views/devices/header'
  import filter from '@/views/devices/filter'

  export default {
    data() {
      return {
        option : {
          tooltip: {
            show: true,
            trigger: "item"
          },
          legend: {
            show: true,
            icon: "circle",
            itemGap: -5,
            textStyle: {
              fontSize: 20,
              padding:20,
              color: "#000"
            },
          },
          radar: {
            radius: "70%",
            startAngle: 90,
            splitNumber: 4,
            shape: "circle",
            splitArea: {
              areaStyle: {
                color: ["transparent"]
              }
            },
            axisLabel: {
              show: false,
              fontSize: 20,
              color: "#000",
              fontStyle: "normal",
              fontWeight: "normal"
            },
            axisLine: {
              show: true,
              lineStyle: {
                type:"dashed",
                color: "#000"
              }
            },
            splitLine: {
              show: true,
              lineStyle: {
                type:"dashed",
                color: "#000"
              }
            },
            indicator: [
              {
                name: "CPU",
                max: 100
              }, {
                name: "内存",
                max: 100
              }, {
                name: "温度",
                max: 100
              }, {
                name: "水位",
                max: 40
              }, {
                name: "压强",
                max: 100
              }, {
                name: "带宽",
                max: 20
              }]
          },
          series: [{
            name: "关键指标",
            type: "radar",
            symbol: "circle",
            symbolSize: 10,
            areaStyle: {
              normal: {
                color: "rgba(86,199,60, 0.4)"
              }
            },
            itemStyle:{
              color:'rgba(86,199,60, 1)',
              borderColor:'rgba(86,199,60, 0.3)',
              borderWidth:10,
            },
            lineStyle: {
              normal: {
                color: "rgba(86,199,60, 1)",
                width: 2
              }
            },
            data: [
              [80, 90, 50, 30, 20, 20]
            ]
          }, {
            name: "实际指标",
            type: "radar",
            symbol: "circle",
            symbolSize: 10,
            itemStyle: {
              normal: {
                color:'rgba(0,183,238, 1)',
                borderColor: "rgba(0,183,238, 0.4)",
                borderWidth: 10
              }
            },
            areaStyle: {
              normal: {
                "color": "rgba(0,183,238, 1)"
              }
            },
            lineStyle: {
              normal: {
                color: "rgba(0,183,238, 1)",
                width: 2,
              }
            },
            data: [
              [12, 50, 25, 20, 70, 5]
            ]
          }]
        }
      }
    },
    methods: {
      _init() {
        this.legendArr = this.myChart.getOption().series
        this.legendArr.forEach((data) => {
          data.selected = true;
        })
        this.$root.charts.push(this.myChart)
        window.addEventListener('resize', function() {
          this.myChart.resize()
        }.bind(this))
      }
    },
    components: {
      'v-header': header,
      'v-filter': filter
    },
    mounted() {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = echarts.init(document.querySelector('.derodar .main'));
      this.myChart.setOption(this.option);
      this._init();
    }
  }

</script>



