<!-- 柱状图 -->
<style lang="stylus" scoped>
  .pie
    height 800px
    //background url('../../assets/bg.jpg') no-repeat
    background-size 100% 100%
    color white
    .main
      width 100%
      height calc(100% - 100px)
      margin-top -15px
</style>

<template>
  <div class="pie">
    <v-header :name="name" :legendArr="legendArr" :myChart="myChart"></v-header>
    <!--<v-filter :myChart="myChart" v-if="myChart._dom"></v-filter>-->
    <div class="main"></div>
  </div>

</template>

<script>
  import echarts from 'echarts'
  import header from '@/views/devices/header'
  import filter from '@/views/devices/filter'

  export default {
    data() {
      return {
        legendArr: [],
        //color: this.$store.state.color,
        color: ['#325B69', '#698570', '#AE5548', '#6D9EA8', '#9CC2B0', '#C98769'],
        myChart: {},
        option: {
          title : {
            text: '水厂网络协议占比',
            // subtext: 'pcap包',
            x:'center',
          },
          tooltip : {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          legend: {
            orient: 'vertical',
            left: 'left',
            //  data: ['Modbus','S7','EnternetIP','FINS','DNP3','ATG']
          },
          textStyle: {
            fontSize: 25 // 用 legend.textStyle.fontSize 更改示例大小
          },
          series : [
            {
              name: '工控网络协议',
              type: 'pie',
              radius : '75%',
              center: ['50%', '60%'],
              data:[
                {value:335, name:'Modbus'},
                {value:310, name:'S7'},
                {value:234, name:'EnternetIP'},
                {value:135, name:'FINS'},
                {value:135, name:'ATG'},
                {value:248, name:'DNP3'}
              ],

              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)',

                }
              }
            }
          ]
        }
      }
    },
    methods: {
      _init() {
        this.legendArr = this.myChart.getOption().series
        this.legendArr.forEach((data) => {
          data.selected = true;
        })
        this.$root.charts.push(this.myChart)
        window.addEventListener('resize', function() {
          this.myChart.resize()
        }.bind(this))
      }
    },
    components: {
      'v-header': header,
      'v-filter': filter
    },
    mounted() {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = echarts.init(document.querySelector('.pie .main'));
      this.myChart.setOption(this.option);
      this._init()
    }
  }

</script>



