<!-- 柱状图 -->
<style lang="stylus" scoped>
  .word
    height 800px
    //background url('../../assets/bg.jpg') no-repeat
    background-size 100% 100%
    color white
    .main
      width 100%
      height calc(100% - 100px)
      margin-top -15px
</style>

<template>
  <div class="word">
    <!--<v-header :name="name" :legendArr="legendArr" :myChart="myChart"></v-header>-->
    <!--<v-filter :myChart="myChart" v-if="myChart._dom"></v-filter>-->
    <div class="main"></div>
  </div>

</template>

<script>
  import echarts from 'echarts'
  import header from '@/views/devices/header'
  import filter from '@/views/devices/filter'
  require('echarts-wordcloud');
  export default {
    data() {
      return {
        legendArr: [],
        //color: this.$store.state.color,
        //color: ['#325B69', '#698570', '#AE5548', '#6D9EA8', '#9CC2B0', '#C98769'],
        myChart: {},
        option:
          {
           title: {
            text: 'SWaT水厂支持的工控协议',
            //link: 'https://www.baidu.com/s?wd=' + encodeURIComponent('ECharts'),
            x: 'left',
            textStyle: {
              fontSize: 23,
              color: 'rgba(0, 0, 0, 0.8)'
            }

          },
         //  backgroundColor: '#131f56',
           tooltip: {
            show: true
          },
           series: [{
            name: '协议名称',
            type: 'wordCloud',
            textPadding: 0,
            autoSize: {
              enable: true,
              minSize: 6
            },
            textStyle: {
              normal: {
                color: function() {
                  return 'rgb(' + [
                    Math.round(Math.random() * 105)+150,
                    Math.round(Math.random() * 105)+150,
                    Math.round(Math.random() * 105)+150
                  ].join(',') + ')';
                }
              },
              emphasis: {
                shadowBlur: 10,
                shadowColor: '#333'
              }
            },
            data:[
              {
                name: "Modbus",
                value: 666
              }, {
                name: "S7",
                value: 457
              }, {
                name: "BACnet",
                value: 999
              }, {
                name: "OPC",
                value: 777
              }, {
                name: "DNP3",
                value: 888
              }, {
                name: "IEC104",
                value: 535
              }, {
                name: "EtherNet/IP",
                value: 856
              }, {
                name: "FINS",
                value: 645
              }, {
                name: "TTEthernet",
                value: 357
              }, {
                name: "Profinet",
                value: 235
              }, {
                name: "openSAEFTY",
                value: 466
              }, {
                name: "RTPs",
                value: 467
              }, {
                name: "KNXnet/IP",
                value: 754
              }, {
                name: "Gryphon",
                value: 357
              }, {
                name: "ZigBee",
                value: 398
              }, {
                name: "IEC",
                value: 633
              }, {
                name: "DeviceNet",
                value: 890
              }, {
                name: "GOOSE",
                value: 235
              }, {
                name: "MMS",
                value: 464
              }, {
                name: "CANopen",
                value: 357
              }, {
                name: "Lontalk",
                value: 326
              }, {
                name: "CDT",
                value: 124
              }, {
                name: "EtherCAT",
                value: 478
              }, {
                name: "CoAP",
                value: 642
              }, {
                name: "HART-IP",
                value: 742
              }, {
                name: "SV(IEC61850)",
                value: 588
              }, {
                name: "BSSAP/BSAP",
                value: 446
              }
            ]/************/
          }]
        }
      }
    },
    methods: {
      _init() {
        this.legendArr = this.myChart.getOption().series
        this.legendArr.forEach((data) => {
          data.selected = true;
        })
        this.$root.charts.push(this.myChart)
        window.addEventListener('resize', function() {
          this.myChart.resize()
        }.bind(this))
      }
    },
    components: {
      'v-header': header,
      'v-filter': filter
    },
    mounted() {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = echarts.init(document.querySelector('.word .main'));
      this.myChart.setOption(this.option);
      this._init()
    }
  }

</script>



