<!-- 散点图 -->
<style lang="stylus" scoped>
  .relation
    //background url('../../assets/bg.jpg') no-repeat
    background-size 100% 100%
    .main
      height calc(100% - 120px)
      width 100%
      transition all 0.5s linear
</style>

<template lang="html">
  <div class="relation">
    <!--<v-header :name="name" :legendArr="legendArr" :myChart="myChart"></v-header>-->
    <!--<v-filter :myChart="myChart" v-if="myChart._dom"></v-filter>-->
    <div class="main"></div>
  </div>
</template>

<script>
  import axios from 'axios'
  import echarts from 'echarts'
  import header from '@/views/devices/header'
  import filter from '@/views/devices/filter'

  const USER_NAME = 'elastic'
  const PSW = 'elasticl@ethical.cn'
  const AUTH_TOKEN = "Basic " + btoa(USER_NAME + ":" + PSW)


  export default {
    created() {
      this._getCityData()
    },
    data() {
      return {
        legendArr: [],
        //color: this.$store.state.color,
        color: ['#325B69', '#698570', '#AE5548', '#6D9EA8', '#9CC2B0', '#C98769'],
        myChart: {},
        reData: {},
        name: '关系结构图'
      }
    },
    methods: {
      _init(options) {
        this.myChart = echarts.init(document.querySelector('.relation .main'))
        this.myChart.setOption(options)
        this.legendArr = options.series
        this.legendArr.forEach((data) => {
          data.selected = true;
        })
        this.$root.charts.push(this.myChart)
        window.addEventListener('resize', function() {
          this.myChart.resize()
        }.bind(this))
      },
      _getCityData() {
        axios.get('static/data/flar.json').then((res) => {
          this.reData = res.data
          this.$nextTick(() => {
            this._getMyChart()
          })
        })
      },
      _getMyChart() {
        let option = {
          title:{
            text:"SWAT工业水厂关系结构图",
            textAlign:'left',
            textStyle: {//标题内容的样式
              color: '#000000'//京东红
            }

          },
          tooltip: {    //提示框组件
            trigger: 'item',    //触发类型，默认：item（数据项图形触发，主要在散点图，饼图等无类目轴的图表中使用）。可选：'axis'：坐标轴触发，主要在柱状图，折线图等会使用类目轴的图表中使用。'none':什么都不触发。
            triggerOn: 'mousemove'    //提示框触发的条件，默认mousemove|click（鼠标点击和移动时触发）。可选mousemove：鼠标移动时，click：鼠标点击时，none：
          },
          series: [    //系列列表
            {
              type: 'tree',    //树形结构

              data: [this.reData],    //上面从flare.json中得到的数据

              top: '1%',       //距离上
              left: '20%',      //左
              bottom: '1%',    //下
              right: '20%',    //右的距离

              symbolSize: 14,   //标记的大小，就是那个小圆圈，默认7

              label: {         //每个节点所对应的标签的样式
                normal: {
                  position: 'left',       //标签的位置
                  verticalAlign: 'middle',//文字垂直对齐方式，默认自动。可选：top，middle，bottom
                  align: 'right',         //文字水平对齐方式，默认自动。可选：top，center，bottom
                  fontSize: 14             //标签文字大小
                }
              },

              leaves: {    //叶子节点的特殊配置，如上面的树图示例中，叶子节点和非叶子节点的标签位置不同
                label: {
                  normal: {
                    position: 'right',
                    verticalAlign: 'middle',
                    align: 'left'
                  }
                }
              },

              expandAndCollapse: true,    //子树折叠和展开的交互，默认打开
              animationDuration: 550,     //初始动画的时长，支持回调函数,默认1000
              animationDurationUpdate: 750//数据更新动画的时长，默认300
            }
          ]
        };
        this._init(option)


      }
    },
    components: {
      'v-header': header,
      'v-filter': filter
    }
  }

</script>



