<!-- 柱状图 -->
<style lang="stylus" scoped>
  .multipleColumn
    height 800px
    //background url('../../assets/bg.jpg') no-repeat
    background-size 100% 100%
    color white
    .main
      width 100%
      height calc(100% - 100px)
      margin-top -15px
</style>

<template>
  <div class="multipleColumn">
    <v-header :name="name" :legendArr="legendArr" :myChart="myChart"></v-header>
    <!--<v-filter :myChart="myChart" v-if="myChart._dom"></v-filter>-->
    <div class="main"></div>
  </div>

</template>

<script>
  import echarts from 'echarts'
  import header from '@/views/devices/header'
  import filter from '@/views/devices/filter'

  export default {
    data() {
      return {
        legendArr: [],
        //color: this.$store.state.color,
        color: ['#325B69', '#698570', '#AE5548', '#6D9EA8', '#9CC2B0', '#C98769'],
        myChart: {},

        option: {
          title: {
            text: '词云展示',
            x: 'left',
            textStyle: {
              fontSize: 23,
              color: 'rgba(255, 255, 255, 0.8)'
            }

          },
          tooltip: {
            show: true
          },
          series: [{
            name: '热点分析',
            type: 'wordCloud',
            textPadding: 0,
            autoSize: {
              enable: true,
              minSize: 6
            },
            textStyle: {
              normal: {
                color: function() {
                  return 'rgb(' + [
                    Math.round(Math.random() * 105)+150,
                    Math.round(Math.random() * 105)+150,
                    Math.round(Math.random() * 105)+150
                  ].join(',') + ')';
                }
              },
              emphasis: {
                shadowBlur: 10,
                shadowColor: '#333'
              }
            },
            data: [
              {
                name: "Jayfee",
                value: 666
              },
              {
                name: "Nancy",
                value: 520
              },
              {
                name: "生活资源",
                value: "999"
              },
              {
                name: "供热管理",
                value: "888"
              },
              {
                name: "交通运输",
                value: "516"
              },
              {
                name: "城市交通",
                value: "515"
              },
              {
                name: "环境保护",
                value: "483"
              }, {
                name: "房地产管理",
                value: "462"
              }, {
                name: "城乡建设",
                value: "449"
              },
              {
                name: "社会保障与福利",
                value: "429"
              }, {
                name: "社会保障",
                value: "407"
              },
            ],
          }]
        }
      }
    },
    methods: {
      _init() {
        this.legendArr = this.myChart.getOption().series
        this.legendArr.forEach((data) => {
          data.selected = true;
        })
        this.$root.charts.push(this.myChart)
        window.addEventListener('resize', function() {
          this.myChart.resize()
        }.bind(this))
      }
    },
    components: {
      'v-header': header,
      'v-filter': filter
    },
    mounted() {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = echarts.init(document.querySelector('.multipleColumn .main'));
      this.myChart.setOption(this.option);
      this._init()
    }
  }

</script>



