<template>
  <el-row class="wrap">
    <el-col :span="24" class="wrap-breadcrumb">
      <el-breadcrumb seporate="/">
        <el-breadcrumb-item :to="{ path: '/'}"><b>首页</b></el-breadcrumb-item>
      </el-breadcrumb>
      <el-col :span="24" class="wrap-main">
        <section class="chart-container">
          <el-row>
            <!--  <el-col :span="8">
              <el-card :body-style="{ padding: '0px'}">
                <img src="../../assets/images/card.jpg">
                <div style="padding: 12px;">
                  <span>标题</span>
                  <div class="bottom clearfix">
                    <time class="time">2018-14-19</time>
                  </div>
                </div>
              </el-card>
            </el-col>
            <el-col :span="8">
              <el-card :body-style="{ padding: '0px'}">
                <img src="../../assets/images/card.jpg">
                <div style="padding: 12px;">
                  <span>标题</span>
                  <div class="bottom clearfix">
                    <time class="time">2018-14-19</time>
                  </div>
                </div>
              </el-card>
            </el-col>
            <el-col :span="8">
              <el-card :body-style="{ padding: '0px'}">
                <img src="../../assets/images/card.jpg">
                <div style="padding: 12px;">
                  <span>标题</span>
                  <div class="bottom clearfix">
                    <time class="time">2018-14-19</time>
                  </div>
                </div>
              </el-card>
            </el-col>-->
            <el-col :span="6">
              <div id="chartPie" style="width: 100%; height: 400px; transform:  scale(0.5)"></div>
            </el-col>
            <el-col :span="6">
              <div id="chartBar" style="width: 100%; height: 400px; transform:  scale(0.5)"></div>
            </el-col>
            <el-col :span="12"></el-col>
            <el-col :span="12"></el-col>
            <el-col :span="24"></el-col>
          </el-row>
        </section>
      </el-col>
    </el-col>
  </el-row>
</template>
<style>
  .time {
    font-size: 13px;
    color: #999;
  }

  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .image {
    width: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }

  .clearfix:after {
    clear: both
  }

  .chart-container {
    width: 100%;
  }
  .chart-container .el-col {
    padding: 30px 20px;
  }
</style>
<script>
  import echarts from 'echarts'
  export default {
    name: 'dashboard',
    data(){
      return {
        chartPie: null,
        chartBar: null
      }
    },
    /*挂载到实例上去之后调用该钩子*/
    mounted (){
      this.drawShape();
    },
    methods: {
      drawShape(){
        this.chartPie = echarts.init(document.getElementById('chartPie'));
        this.chartBar = echarts.init(document.getElementById('chartBar'));

        this.chartPie.setOption({
          title : {
            text: '水厂网络协议饼图',
            // subtext: 'pcap包',
            x:'center'
          },
          tooltip : {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          legend: {
            orient: 'vertical',
            left: 'left',
            //  data: ['Modbus','S7','EnternetIP','FINS','DNP3','ATG']
          },
          textStyle: {
            fontSize: 20 // 用 legend.textStyle.fontSize 更改示例大小
          },
          series : [
            {
              name: '工控网络协议',
              type: 'pie',
              radius : '75%',
              center: ['50%', '60%'],
              data:[
                {value:335, name:'Modbus'},
                {value:310, name:'S7'},
                {value:234, name:'EnternetIP'},
                {value:135, name:'FINS'},
                {value:135, name:'ATG'},
                {value:248, name:'DNP3'}
              ],

              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)',

                }
              }
            }
          ]
        });

        this.chartBar.setOption({
          color: ['#3398DB'],
          tooltip : {
            trigger: 'axis',
            axisPointer : {            // 坐标轴指示器，坐标轴触发有效
              type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
            }
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          xAxis : [
            {
              type : 'category',
              data : ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
              axisLabel: { show: true, textStyle: { color: '#000', fontSize:'10' } },
              axisTick: {
                alignWithLabel: true
              }
            }
          ],
          yAxis : [
            {
              type : 'value',
              axisLabel: { show: true, textStyle: { color: '#000', fontSize:'17' } },
            }
          ],
          series : [
            {
              name:'直接访问',
              type:'bar',
              barWidth: '60%',
              data:[10, 52, 200, 334, 390, 330, 220]
            }
          ]
        });
      }
    }
  }
</script>



