    <strong style="font-family: 黑体;font-size: x-large;text-align:left">用户日志</strong>
    <div class="select">
      <el-table ref="Table" :data="tableData" tooltip-effect="dark" style="width:100%" border="true" height="250">
        <el-table-column label="日志编号" prop="id" ></el-table-column>
        <el-table-column prop="name" label="操作模块" ></el-table-column>
        <el-table-column prop="type" label="请求IP" ></el-table-column>
        <el-table-column prop="people" label="用户编号" p></el-table-column>
        <el-table-column prop="username" label="用户名称" p></el-table-column>
        <el-table-column prop="dictionary" label="角色名称" ></el-table-column>
        <el-table-column prop="date" label="操作行为" ></el-table-column>
        <el-table-column prop="job" label="请求结果" ></el-table-column>
        <el-table-column prop="detail" label="请求时间" ></el-table-column>

        <!--<el-table-column prop="detail" label="略策详情" width="120">-->
        <!--<template slot-scope="scope">-->
        <!--<el-button @click="showDetail" type="text" size="small">详情</el-button>-->
        <!--</template>-->
        <!--</el-table-column>-->
      </el-table>
      <el-col :span="20" class="toolbar">
        <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange"
                       :current-page="currentPage" :page-sizes="[10, 50, 100, 200]" :page-size="10"
                       layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
      </el-col>
    </div>
