<script>

  const cityOptions = ['超级管理员', '管理员', '审计员', '普通用户'];
  export default {
    data() {
      return {
        total:5,
        checkedCities: [],
        formLabelWidth: '120px',
        dialogAddVisible:false,
        dialogdescripption:false,
        cities: cityOptions,
        isIndeterminate: true,
        show3:false,
        selectItem:{
          user:'',
          factory:[],
          position:'',
          depart:''
        },
        form: {
          nickname:'',
          realname:'',
          factory: '',
          number: '',
          department: '',
          content: '1',
          remark: '',
          createTime: '',
        },
        factory_list:['辽宁水厂','广东水厂','北京水厂','哈尔滨水厂','吉林水厂','陕西水厂'],
        depart_list:['综合管理部','财务部','安全监察部','二供服务部','工程业务部','生产技术部'],
        tableData: [{
          id: '1',
          name: '设备管理',
          type:"219.216.86.145",
          people: '001',
          dictionary: '管理员',
          username:"admin",
          date: '正常',
          job:"正常",
          detail: '2018-11-18'
        }, {
          id: '2',
          name: '安全审计',
          type:"219.216.86.122",
          people: '001',
          dictionary: '管理员',
          username:"admin",
          date: '正常',
          job:"正常",
          detail: '2018-11-18'
        },
          {
            id: '3',
            name: '用户管理',
            type:"219.216.86.122",
            people: '001',
            dictionary: '管理员',
            username:"admin",
            date: '正常',
            job:"正常",
            detail: '2018-11-18'
          },
          {
            id: '4',
            name: '安全审计',
            type:"219.216.86.122",
            people: '001',
            dictionary: '管理员',
            username:"admin",
            date: '正常',
            job:"正常",
            detail: '2018-11-18'
          },
          {
            id: '5',
            name: '安全审计',
            type:"219.216.86.122",
            people: '001',
            dictionary: '管理员',
            username:"admin",
            date: '正常',
            job:"正常",
            detail: '2018-11-19'
