    methods: {
      openMsg() {
        this.$message({
          showClose: true,
          message: '添加成功！',
          type: 'success'
        });
        this.dialogAddVisible=false;
      },
      startUse() {
        this.$message({
          showClose: true,
          message: '启用成功！',
          type: 'success'
        });
      },
      onSubmit() {
        console.log('submit!');
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      showAddDialog(){
        console.log(this.dialogAddVisible);
        this.dialogAddVisible = true;
      },
      showDetail(){
        console.log(this.dialogdescripption);
        this.dialogdescripption = true;

      },
      handleCheckedCitiesChange(value) {
        let checkedCount = value.length;
        this.checkAll = checkedCount === this.cities.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length;
      },
      handleClose(done){  //关闭弹窗
        done();
      }
    }
  }
</script>
<style>
  .select{
    position: relative;
    width: 100%;
    margin: 0 auto;
    background:#fff;
    padding: 20px;
    border-radius: 10px;
    overflow: hidden;
  }
  .btndiv{
    width: 100%;
    margin:20px 0px 10px 0px;
    text-align: left;
  }
  .mybutton{
    display: inline-block;
    margin-left:5px;
    margin-right: 5px;
  }
</style>




