    <el-col :span="23" class="warp-main" v-loading="loading" element-loading-text="拼命加载中" style="margin-top: 15px">
      <el-row>
        <el-col :span="4">
          <div class="grid-content bg-purple">
            <el-upload align="center"
                       class="avatar-uploader"
