            </el-upload>
            <a href="#" style="display: block;font-family: 新宋体;font-size: large;text-align: center;margin-top: 10px">修改头像</a>
          </div>
        </el-col>
        <el-col :span="20">
          <el-row  style="margin-left: 10px;">
            <el-col span="24" style="margin-bottom: -10px;">
              <el-form label-position="left" class="info-table-id">
                <el-form-item label="ID：">
                  <span>admin</span>
                </el-form-item>
              </el-form>
            </el-col>
            <el-col span="2" style="margin-top: -20px">
              <el-form>
                <el-form-item label="关注">
                  <span>3</span>
                </el-form-item>
              </el-form>
            </el-col>
            <el-col span="2" style="margin-top: -20px">
