                </el-form-item>
              </el-form>
            </el-col>
            <el-col span="1" style="margin-top: -20px">
              <el-form>
                <el-form-item label="|">
                </el-form-item>
              </el-form>
            </el-col>
            <el-col span="2" style="margin-top: -10px">
              <a href="#">充值</a>
            </el-col>
            <el-col span="24">
              <hr style="margin-top: -15px;"/>
            </el-col>
          </el-row>
          <el-row>
            <el-col span="21" style="margin-left: 10px">
              <div class="grid-content">
                <el-form label-position="left" class="info-table-expand">
                  <el-form-item label="昵称：" style="margin-bottom: 0px">
                    <span>admin</span>
                  </el-form-item>
                  <el-form-item label="实名：" style="margin-bottom: 0px">
                    <span></span>
                  </el-form-item>
                  <el-form-item label="性别：" style="margin-bottom: 0px">
                    <span></span>
                  </el-form-item>
                  <el-form-item label="生日：" style="margin-bottom: 0px">
                    <span></span>
                  </el-form-item>
                  <el-form-item label="工厂：" style="margin-bottom: 0px">
                    <span></span>
                  </el-form-item>
                  <el-form-item label="部门：" style="margin-bottom: 0px">
                    <span></span>
                  </el-form-item>
                  <el-form-item label="职位：" style="margin-bottom: 0px">
                    <span></span>
                  </el-form-item>
                  <el-form-item label="简介：" style="margin-bottom: 0px">
                    <span></span>
                  </el-form-item>
                </el-form>
              </div>
            </el-col>
            <el-col span="2">
              <a href="#" @click="showAddDialog" style="float: right;font-family: 新宋体;font-size: large">修改资料</a>
            </el-col>
          </el-row>
        </el-col>

      </el-row>

    </el-col>

    <el-dialog title="修改资料" :close-on-press-escape="false" :close-on-click-modal="false" :visible.sync="dialogAddVisible" :before-close="handleClose">
      <el-form :model="form">
        <el-form-item label="昵称：" :label-width="formLabelWidth">
          <el-input v-model="form.nickname" value="45765trfd"></el-input>
        </el-form-item>
        <el-form-item label="实名：" :label-width="formLabelWidth">
          <el-input v-model="form.realname" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="性别：" :label-width="formLabelWidth">
          <el-radio-group v-model="form.content">
            <el-radio label="1">男</el-radio>
            <el-radio label="2">女</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="生日：" :label-width="formLabelWidth">
          <el-date-picker v-model="form.createTime" type="date" placeholder="请选择日期"></el-date-picker>
        </el-form-item>
        <el-form-item label="工厂：" :label-width="formLabelWidth">
          <el-select v-model="form.factory" placeholder="请选择共产">
            <el-option label="辽宁水厂" value="liaoning"></el-option>
            <el-option label="广东水厂" value="guangdong"></el-option>
            <el-option label="北京水厂" value="beijing"></el-option>
            <el-option label="哈尔滨水厂" value="haerbing"></el-option>
            <el-option label="吉林水厂" value="jilin"></el-option>
            <el-option label="陕西水厂" value="shanxi"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="部门：" :label-width="formLabelWidth">
          <el-select v-model="form.department" placeholder="请选择部门">
            <el-option label="综合管理部" value="zonghe"></el-option>
            <el-option label="财务部" value="caiwu"></el-option>
            <el-option label="安全监察部" value="anquan"></el-option>
            <el-option label="二供服务部" value="ergong"></el-option>
            <el-option label="工程业务部" value="gongcheng"></el-option>
            <el-option label="生产技术部" value="shengchan"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="职位：" :label-width="formLabelWidth">
          <el-input v-model="form.text" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="简介：" :label-width="formLabelWidth">
          <el-input v-model="form.remark" type="textarea" placeholder="300字以内"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="dialogAddVisible = false">取消</el-button>
        <el-button type="primary" @click="openMsg">提交</el-button>
      </div>
    </el-dialog>

  </div>
</template>
<style>
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 50%;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }

  .info-table-expand {
    font-size: 20px;
  }
  .info-table-expand label {
    width: 90px;
  }
  .info-table-id {
    font-size: 20px;
    margin-bottom: 0%;
  }
  .info-table-id label {
    width: 90px;
    color: #99a9bf;
  }
  .info-table-id span {
    width: 90px;
    color: #99a9bf;
  }
</style>
<script>
  export default {
    name: 'mission',
    data: function(){
      return {
        loading: false,
        radio: '1',
        idShow: false,
        keyword: "集团",
        total: 2,
        currentPage: 1,
        pageSize: 10,
        tableData: [{
          mId: 1,
          mNumber: '10001',
          mType: '接收订单',
          mContent: '接乘客',
          eRemark: '无',
          createTime: '2017-03-27',
          updateTime: '2016-03-27',
          recordStatus: '1'
        }],
        form: {
          nickname:'',
          realname:'',
          factory: '',
          number: '',
          department: '',
          content: '1',
          remark: '',
          createTime: '',
        },
        formLabelWidth: '120px',
        dialogAddVisible: false,
        filters: {
          number: ''
        },
        imageUrl: '../static/img/bg.jpg'
      };
    },
    methods: {
      openMsg() {
        this.$message({
          showClose: true,
          message: '修改成功！',
          type: 'success'
        });
        this.dialogAddVisible=false;
      },
      handleSearch(){
        console.info(this.filters.number);
      },
      handleDetail(index, row) {
        console.log(index, row);
        console.log(row.mId);
      },
      handleDelete(index, row) {
        console.log(index, row);
      },
      handleSizeChange(val){
//        this.pageSize = val;
//        this.currentPage = 1;
      },
      handleCurrentChange(val){
//        this.currentPage = val;
      },
      showAddDialog(){
        this.dialogAddVisible = true;
      },
      handleClose(done){  //关闭弹窗
        done();
      },
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      }
    }
  }
</script>



