<!-- 柱状图 -->
<style lang="stylus" scoped>
  .meter
    height 800px
    //background url('../../assets/bg.jpg') no-repeat
    background-size 100% 100%
    color white
    .main
      width 100%
      height calc(100% - 100px)
      margin-top -15px
</style>

<template>
  <div class="meter">
    <div class="main"></div>
  </div>

</template>

<script>
  import echarts from 'echarts'
  import header from '@/views/devices/header'
  import filter from '@/views/devices/filter'

  export default {
    data() {
      return {
        legendArr: [],
        myChart: {},
        name: '柱状图',
        option:{
          title: {
            text: '关键指标检测',
            textStyle: {//标题内容的样式
              color: '#000000'//
            }
          },
          tooltip: {
            formatter: "{a} <br/>{b} : {c}%"
          },
          series: [{
            name: "仪盘表",
            type: "gauge",
            splitNumber:60,
            axisLine: {
              "lineStyle": {
                "color": [
                  [0.31, "#333"],
                  [1, "#333"]
                ],
                "width": 20
              },

            },
            axisTick: {
              lineStyle: {
                color:'#ccc',
                width: 2
              },
              length: 20,
              splitNumber:2
            },
            pointer: {           // 分隔线
              shadowColor : '#fff', //默认透明
              shadowBlur: 5,
              show:false
            },
            axisLabel: {
              distance: 10,
              textStyle: {
                color: "#fff"
              },
              show:false,
            },
            splitLine: {
              "show": false
            },
            itemStyle: {
              normal: {
                color: "#494f50"
              }
            },
            detail: {
              formatter: "{value}%",
              offsetCenter: [0, "30%"],
              textStyle: {
                fontSize: 60,
                color: "#F37B1D"
              }
            },
            title: {
              offsetCenter: [0, "60%"]
            },
            data: [{
              name: "CPU",
              value: 31
            }]
          }]
        }
      }
    },
    methods: {
      _init() {
        this.legendArr = this.myChart.getOption().series
        this.legendArr.forEach((data) => {
          data.selected = true;
        })
        this.$root.charts.push(this.myChart)
        window.addEventListener('resize', function() {
          this.myChart.resize()
        }.bind(this))
      },
      detectionData(str) {
        var color = new echarts.graphic.LinearGradient(0, 0, 1, 1, [{
          offset: 0,
          color: 'rgba(243, 238,67, 1)'
        }, {
          offset: 0.5,
          color: 'rgba(225, 128,78, 1)'
        }, {
          offset:1,
          color: 'rgba(237, 92,79, 1)'
        }]);
        if (str >= 30 && str <= 60) {
          color =  new echarts.graphic.LinearGradient(0, 0, 1, 1, [{
            offset: 0,
            color: 'rgba(243, 238,67, 1)'
          }, {
            offset: 0.5,
            color: 'rgba(225, 128,78, 1)'
          }, {
            offset:1,
            color: 'rgba(237, 92,79, 1)'
          }]);
        } else if (str > 60) {
          color = new echarts.graphic.LinearGradient(0, 0, 1, 1, [{
            offset: 0,
            color: 'rgba(243, 238,67, 1)'
          }, {
            offset: 0.5,
            color: 'rgba(225, 128,78, 1)'
          }, {
            offset:1,
            color: 'rgba(237, 92,79, 1)'
          }]);
        }
        return color;
      },
      refreshData(){
        setInterval(()=>{
          var value = (Math.random() * 100).toFixed(2) - 0;
          this.option.series[0].data[0].value = value;
          this.option.series[0].axisLine.lineStyle.color[0][0] = value / 100;
          this.option.series[0].axisLine.lineStyle.color[0][1] = this.detectionData(value);
          this.myChart.setOption(this.option, true);
        }, 500);
      }
    },
    components: {
      'v-header': header,
      'v-filter': filter
    },
    mounted() {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = echarts.init(document.querySelector('.meter .main'));
      this.myChart.setOption(this.option);
      this._init();
      this.refreshData();
    }
  }

</script>



