<!-- 柱状图 -->
<style lang="stylus" scoped>
  .pierose
    height 800px
    //background url('../../assets/bg.jpg') no-repeat
    background-size 100% 100%
    color white
    .main
      width 100%
      height calc(100% - 100px)
      margin-top -15px
</style>

<template>
  <div class="pierose">
    <v-header :name="name" :legendArr="legendArr" :myChart="myChart"></v-header>
    <!--<v-filter :myChart="myChart" v-if="myChart._dom"></v-filter>-->
    <div class="main"></div>
  </div>

</template>

<script>
  import echarts from 'echarts'
  import header from '@/views/devices/header'
  import filter from '@/views/devices/filter'

  export default {
    data() {
      return {
        legendArr: [],
        //color: this.$store.state.color,
        color: ['#325B69', '#698570', '#AE5548', '#6D9EA8', '#9CC2B0', '#C98769'],
        myChart: {},
        option : {
          //ackgroundColor: '#2c343c',

          title: {
            text: 'Customized Pie',
            left: 'center',
            top: 20,
            textStyle: {
              color: '#000000'
            }
          },

          tooltip : {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },

          visualMap: {
            show: false,
            min: 80,
            max: 600,
            inRange: {
              colorLightness: [0, 1]
            }
          },
          series : [
            {
              name:'访问来源',
              type:'pie',
              radius : '75%',
              center: ['50%', '50%'],
              data:[
                {value:335, name:'DNS攻击'},
                {value:310, name:'恶意广告'},
                {value:274, name:'URL注入'},
                {value:235, name:'SQL注入'},
                {value:400, name:'DoS / DDoS攻击'}
              ].sort(function (a, b) { return a.value - b.value; }),
              roseType: 'radius',
              label: {
                normal: {
                  textStyle: {
                    color: 'rgba(255, 255, 255, 1)'
                  }
                }
              },
              labelLine: {
                normal: {
                  lineStyle: {
                    color: 'rgba(255, 255, 255, 0.3)'
                  },
                  smooth: 0.2,
                  length: 10,
                  length2: 20
                }
              },
              itemStyle: {
                normal: {
                  color: '#c23531',
                  shadowBlur: 200,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              },

              animationType: 'scale',
              animationEasing: 'elasticOut',
              animationDelay: function (idx) {
                return Math.random() * 200;
              }
            }
          ]
        }
      }
    },
    methods: {
      _init() {
        this.legendArr = this.myChart.getOption().series
        this.legendArr.forEach((data) => {
          data.selected = true;
        })
        this.$root.charts.push(this.myChart)
        window.addEventListener('resize', function() {
          this.myChart.resize()
        }.bind(this))
      }
    },
    components: {
      'v-header': header,
      'v-filter': filter
    },
    mounted() {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = echarts.init(document.querySelector('.pierose .main'));
      this.myChart.setOption(this.option);
      this._init()
    }
  }

</script>



