<template>
  <div class="infocontainer" align="center">
    <strong style="font-family: 黑体;font-size: xx-large;margin-bottom: 50px">设备审计</strong>
    <el-row style="margin-top: 50px">
      <el-col span="24">
        <el-form inline="true" size="mini" model="selectItem">
          <el-form-item label="设备名称">
            <el-input v-model="selectItem.user" placeholder="请输入设备名称"></el-input>
          </el-form-item>
          <el-form-item label="所属水厂">
            <el-select v-model="selectItem.factory" placeholder="请选择水厂">
              <el-option v-for="item in factory_list" :value="item" :key="item">{{item}}</el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="所属部门">
            <el-select v-model="selectItem.depart" placeholder="请选择部门">
              <el-option v-for="item in depart_list" :value="item" :key="item">{{item}}</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="设备类型">
            <el-select v-model="selectItem.factory" placeholder="请选择设备类型">
              <el-option v-for="item in device_type" :value="item" :key="item">{{item}}</el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <el-form label-position="center">
          <el-form-item>
            <el-button type="primary" @click="onSubmit">查询</el-button>
          </el-form-item>
          <el-form-item>
            <h3>{{select_callback_info}}</h3>
          </el-form-item>
        </el-form>
      </el-col>
      <el-col :span="12" offset="1">
        <div class="grid-content bg-purple">
          <el-table @row-click="showCard" :data="tableData2" style="width: 100%" :row-class-name="tableRowClassName" default-sort="{prop: 'level', order: 'descending'}">
            <el-table-column prop="num" label="设备编号" width="120px" align="center"></el-table-column>
            <el-table-column prop="name" label="设备名称" width="150px" align="center"></el-table-column>
            <el-table-column prop="factory" label="所属水厂" align="center"></el-table-column>
            <el-table-column prop="adr" label="所属车间" align="center"></el-table-column>
            <el-table-column prop="status" label="状态" width="50px" align="center"></el-table-column>
            <el-table-column prop="degree" label="威胁程度" align="center"></el-table-column>
            <el-table-column prop="level" label="告警次数" align="center"  sortable="true"></el-table-column>
          </el-table>
        </div>
      </el-col>
      <el-col :span="10">
        <div style="width: 50%;float: left" >
          <h1 style="font-family: 宋体;font-size: x-large">设备评分</h1>
          <div id="chartBar" style="width: 100%; height:250px;margin-top: -30px; transform:  scale(1)"></div>
        </div>
        <div style="width: 50%;float: left;height: 250px" >
          <h1 style="font-family: 宋体;font-size: x-large">设备指标</h1>
          <div style="transform: translate(-0.65%,-25%) scale(1)">
            <derador></derador>
          </div>
        </div>
        <div style="margin-top: -50px">
          <el-card class="box-card">
            <div class="text item">
              <p>威胁因素</p><br/>
              <p style="font-family: 宋体;font-size: medium">无</p>
            </div>
            <div class="text item">
              <p>告警原因</p><br/>
              <p style="font-family: 宋体;font-size: medium">出水口浊度大于阈值3，ph值大于阈值</p>
            </div>
            <div class="text item">
              <p>建议</p>
              <br/>
              <p style="font-family: 宋体;font-size: medium">查看plc日志，是否被远程控制，修改了各个浊度段对应的控制输出电流初值；远程修改plc的出口浊度的阈值为5</p>
            </div>
          </el-card>
        </div>
        <!--<el-row>
          <el-col span="12" style="margin: auto">

          </el-col>
          <el-col span="12" >
           <div style="transform: translate(-0.65%,-25%) scale(0.9)">
             <derador></derador>
           </div>
          </el-col>
          <el-col span="24" style="margin-top: 20px">
            <el-card class="box-card">
              <div class="text item">
                <p>威胁因素</p><br/>
                <p style="font-family: 宋体;font-size: medium">无</p>
              </div>
              <div class="text item">
                <p>告警原因</p><br/>
                <p style="font-family: 宋体;font-size: medium">出水口浊度大于阈值3，ph值大于阈值</p>
              </div>
              <div class="text item">
                <p>建议</p>
                <br/>
                <p style="font-family: 宋体;font-size: medium">查看plc日志，是否被远程控制，修改了各个浊度段对应的控制输出电流初值；远程修改plc的出口浊度的阈值为5</p>
              </div>
            </el-card>
          </el-col>
        </el-row>-->
        <div>



        </div>


      </el-col>
    </el-row>

  </div>

</template>
<script>
  import echarts from 'echarts'
  import derador from '@/views/security/derodar.vue'
  export default {
    name: 'devicesaudit',


    data() {
      return {
        chartBar: null,
        selectItem:{
          user:'',
          factory:[],
          position:'',
          depart:''
        },
        factory_list:['辽宁水厂','广东水厂','北京水厂','哈尔滨水厂','吉林水厂','陕西水厂'],
        depart_list:['综合管理部','财务部','安全监察部','二供服务部','工程业务部','生产技术部'],
        device_type:['PLC','RPU','DSC','摄像头'],
        cardshow: false,
        value5: 3.7,
        tableData2: [{
          num: 'PLC_001_001',
          name: 'PLC Super E50',
          factory: '辽宁水厂',
          adr: '第一车间',
          status: "在线",
          degree: '中',
          level: '34'
        }, {
          num: 'DSC_001_001',
          name: 'DST 2200',
          factory: '辽宁水厂',
          status: "在线",
          adr: '第一车间',
          degree: '低',
          status: "在线",
          level: '92'
        }, {
          num: 'RTU_001_001',
          name: '4G-LTE DTU',
          factory: '辽宁水厂',
          adr: '第一车间',
          status: "离线",
          degree: '中',
          level: '90'
        }, {
          num: 'DSC_001_001',
          name: 'Setline DSC',
          factory: '辽宁水厂',
          adr: '第一车间',
          status: "在线",
          degree: '高',
          level: '89'
        }, {
          num: 'PLC_001_002',
          name: 'PLC Super E61',
          factory: '辽宁水厂',
          adr: '第一车间',
          status: "在线",
          degree: '高',
          level: '87'
        }]
      }
    },
    components:{
      derador
    },
    mounted (){
      this.drawShape();
    },
    methods: {
      drawShape(){
        this.chartBar = echarts.init(document.getElementById('chartBar'));

        this.chartBar.setOption({
          title: {
            text: '80分',
            x: 'center',
            y: 'center',
            textStyle: {
              fontWeight: 'normal',
              color: '#0580f2',
              fontSize: '50'
            }
          },
          color: ['rgba(176, 212, 251, 1)'],
          legend: {
            show: false,
            itemGap: 12,
            data: ['01', '02']
          },

          series: [{
            name: 'Line 1',
            type: 'pie',
            clockWise: true,
            radius: ['50%', '66%'],
            itemStyle: {
              normal: {
                label: {
                  show: false
                },
                labelLine: {
                  show: false
                }
              }
            },
            hoverAnimation: false,
            data: [{
              value: 80,
              name: '01',
              itemStyle: {
                normal: {
                  color: { // 完成的圆环的颜色
                    colorStops: [{
                      offset: 0,
                      color: '#00cefc' // 0% 处的颜色
                    }, {
                      offset: 1,
                      color: '#367bec' // 100% 处的颜色
                    }]
                  },
                  label: {
                    show: false
                  },
                  labelLine: {
                    show: false
                  }
                }
              }
            }, {
              name: '02',
              value: 20
            }]
          }]
        });
      },
      tableRowClassName({row, rowIndex}) {
        if (rowIndex === 1) {
          return 'low-row';
        } else if (rowIndex === 3 || rowIndex === 4) {
          return 'high-row';
        } else if (rowIndex === 0 || rowIndex === 2){
          return 'medium-row';
        }
        return '';
      },
      showCard(){
        if(this.cardshow===true)
          this.cardshow=false
        else
          this.cardshow=true
      },
    },

  }
</script>
<style>

  .bg-purple {
    background: #d3dce6;
  }
  .font {
    font-family: 宋体;
    font-size: x-large;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }

  .infocontainer {
    padding-top: 50px;
  }
  .eltag{
    padding-top: 20px;
  }
  .el-table .medium-row {
    background: #fff6f6;
  }
  .el-table .low-row {
    background: #fffdf4;
  }
  .el-table .high-row {
    background: #ffdad8;
  }
  .text {
    font-size: 20px;
  }

  .item {
    padding: 10px 0;
  }

  .box-card {
    width: 90%;
    margin-top: -50px;
  }

</style>



