<!-- 柱状图 -->
<style lang="stylus" scoped>
  .columnChart
    height 800px
    //background url('../../assets/bg.jpg') no-repeat
    background-size 100% 100%
    color white
    .main
      width 100%
      height calc(100% - 100px)
      margin-top -15px
</style>

<template>
  <div class="columnChart">
    <!--<v-header :name="name" :legendArr="legendArr" :myChart="myChart"></v-header>-->
    <!--<v-filter :myChart="myChart" v-if="myChart._dom"></v-filter>-->
    <div class="main"></div>
  </div>

</template>

<script>
  import echarts from 'echarts'
  import header from '@/views/devices/header'
  import filter from '@/views/devices/filter'

  export default {
    data() {
      return {
        legendArr: [],
        //color: this.$store.state.color,
        color: ['#325B69', '#698570', '#AE5548', '#6D9EA8', '#9CC2B0', '#C98769'],
        myChart: {},
        name: '水厂网络漏洞数量统计图',
        option: {
          title: {
            text: '水厂网络漏洞数量统计图',
            //subtext: '数据来自网络'
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'shadow'
            }
          },
          legend: {
            data: ['2018年9月','2018年10月', '2018年11月']
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          xAxis: {
            type: 'value',
            boundaryGap: [0, 0.01]
          },
          yAxis: {
            type: 'category',
            //color:"black",
            data: ['CVE-2017-14024','CVE-2017-15583','CVE-2017-14023','CVE-2017-12719','CVE-2017-14016','CVE-2017-12729']
          },
          series: [
            {
              name: '2018年9月',
              type: 'bar',
              data: [912, 864, 874, 800, 809, 777]
            },
            {
              name: '2018年10月',
              type: 'bar',
              data: [895, 546, 467, 800, 521, 490]
            },
            {
              name: '2018年11月',
              type: 'bar',
              data: [655, 398, 591, 490, 577, 321]
            }

          ]
        }
      }
    },
    methods: {
      _init() {
        this.legendArr = this.myChart.getOption().series
        this.legendArr.forEach((data) => {
          data.selected = true;
        })
        this.$root.charts.push(this.myChart)
        window.addEventListener('resize', function() {
          this.myChart.resize()
        }.bind(this))
      }
    },
    components: {
      'v-header': header,
      'v-filter': filter
    },
    mounted() {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = echarts.init(document.querySelector('.columnChart .main'));
      this.myChart.setOption(this.option);
      this._init()
    }
  }

</script>



