<template>
  <div style="margin-top: 20px;margin-left: 2%;width: 95%" align="center" >
    <strong style="font-family: 黑体;font-size: x-large;text-align: center">策略列表</strong>
    <div class="select">
      <el-form inline="true" size="mini" model="selectItem">
        <el-form-item label="解决问题类别:">
          <el-input v-model="selectItem.user" placeholder="请输入类别"></el-input>
        </el-form-item>
        <el-form-item label="策略名:">
          <el-input v-model="selectItem.user" placeholder="请输入策略名"></el-input>
        </el-form-item>
        <el-form-item label="策略编号:">
          <el-input v-model="selectItem.position" placeholder="请输入策略名"></el-input>
        </el-form-item>
      </el-form>
      <el-form label-position="center">
        <el-form-item>
          <el-button type="primary" @click="onSubmit">查询</el-button>
        </el-form-item>
        <el-form-item>
          <h3>{{select_callback_info}}</h3>
        </el-form-item>
      </el-form>
      <div class="btndiv">
        <el-dialog title="添加策略" :close-on-press-escape="false" :close-on-click-modal="false" :visible.sync="dialogAddVisible" :before-close="handleClose">
          <el-form :model="form">
            <el-form-item label="策略名称：" :label-width="formLabelWidth">
              <el-input v-model="form.nickname" value="45765trfd"></el-input>
            </el-form-item>
            <el-form-item label="策略编号：" :label-width="formLabelWidth">
              <el-input v-model="form.realname" auto-complete="off"></el-input>
            </el-form-item>
            <!--<el-form-item label="性别：" :label-width="formLabelWidth">-->
            <!--<el-radio-group v-model="form.content">-->
            <!--<el-radio label="1">男</el-radio>-->
            <!--<el-radio label="2">女</el-radio>-->
            <!--</el-radio-group>-->
            <!--</el-form-item>-->
            <el-form-item label="更新日期：" :label-width="formLabelWidth">
              <el-date-picker v-model="form.createTime" type="date" placeholder="请选择日期"></el-date-picker>
            </el-form-item>
            <el-form-item label="策略描述：" :label-width="formLabelWidth">
              <el-input v-model="form.remark" type="textarea" placeholder="300字以内"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click.native="dialogAddVisible = false">取消</el-button>
            <el-button type="primary" @click="openMsg">提交</el-button>
          </div>
        </el-dialog>

        <el-dialog title="详情" :close-on-press-escape="false" :close-on-click-modal="false" :visible.sync="dialogdescripption" :before-close="handleClose">
          <el-form :model="form">
            <el-form-item label="策略名称：" :label-width="formLabelWidth">
              sat
            </el-form-item>
            <el-form-item label="策略编号：" :label-width="formLabelWidth">
              w_001
            </el-form-item>
            <el-form-item label="更新日期：" :label-width="formLabelWidth">
              2018-11-16
            </el-form-item>
            <el-form-item label="策略描述：" :label-width="formLabelWidth">
              硬件漏洞检测
            </el-form-item>
            <el-form-item label="具体操作：" :label-width="formLabelWidth">
              更换plc硬件设备
            </el-form-item>
            <el-form-item label="规则描述：" :label-width="formLabelWidth">
              alert tcp any any -> 192.168.1.0/24 111 (content:"|00 01 86 a5|"; msg: "mountd access";)
            </el-form-item>
          </el-form>
          <!--<div slot="footer" class="dialog-footer">-->
          <!--<el-button @click.native="dialogdescripption = false">取消</el-button>-->
          <!--<el-button type="primary" @click="openMsg">提交</el-button>-->
          <!--</div>-->
        </el-dialog>

        <el-button class="mybutton"  size="large" @click="showAddDialog" icon="el-icon-third-tianjiayonghu"> 添加策略</el-button>
        <el-button class="mybutton" @click="deleteUser" size="large" icon="el-icon-third-delete_member"> 删除选中</el-button>
        <el-button class="mybutton" @click="startUse" size="large" icon="el-icon-third-role">启用策略</el-button>

        <el-collapse-transition  name="slide-fade">
          <div v-show="show3">
            <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange" style="float: left;margin-top: 10px" >
              <el-checkbox v-for="city in cities" :label="city" :key="city">{{city}}</el-checkbox>
            </el-checkbox-group>
            <el-button type="primary" @click="sumbit" style="margin-left: 20px;height: 30px;margin-top: 5px;text-align: center">分配</el-button>
          </div>
        </el-collapse-transition>
        <el-button class="mybutton" @click="refresh_table" icon="el-icon-third-refresh-1-copy"></el-button>
      </div>

      <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange" border="true" height="250">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column label="略策编号" prop="id" ></el-table-column>
        <el-table-column prop="name" label="略策名称" ></el-table-column>
        <el-table-column prop="type" label="策略类型" ></el-table-column>
        <el-table-column prop="people" label="策略创建人" p></el-table-column>
        <el-table-column prop="dictionary" label="策略描述" ></el-table-column>
        <el-table-column prop="date" label="略策创建日期" ></el-table-column>
        <!--<el-table-column prop="job" label="职位" ></el-table-column>-->
        <!--<el-table-column prop="detail" label="略策详情" show-overflow-tooltip></el-table-column>-->
        <el-table-column prop="detail" label="略策详情" width="120">
          <template slot-scope="scope">
            <el-button @click="showDetail" type="text" size="small">详情</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-col :span="24" class="toolbar">
        <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange"
                       :current-page="currentPage" :page-sizes="[10, 50, 100, 200]" :page-size="10"
                       layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
      </el-col>
    </div>
  </div>
</template>

<script>
  const cityOptions = ['超级管理员', '管理员', '审计员', '普通用户'];
  export default {
    data() {
      return {
        total:2,
        checkedCities: [],
        formLabelWidth: '120px',
        dialogAddVisible:false,
        dialogdescripption:false,
        cities: cityOptions,
        isIndeterminate: true,
        show3:false,
        selectItem:{
          user:'',
          factory:[],
          position:'',
          depart:''
        },
        form: {
          nickname:'',
          realname:'',
          factory: '',
          number: '',
          department: '',
          content: '1',
          remark: '',
          createTime: '',
        },
        factory_list:['辽宁水厂','广东水厂','北京水厂','哈尔滨水厂','吉林水厂','陕西水厂'],
        depart_list:['综合管理部','财务部','安全监察部','二供服务部','工程业务部','生产技术部'],
        tableData: [{
          id: 'w_001',
          name: 'sat',
          type:"s",
          people: '陈俊',
          dictionary: '硬件漏洞检测',
          date: '2018-11-16',
          detail: '需要加强硬件扫描'
        }, {
          id: 'w_002',
          name: 'plc',
          type:"s",
          people: '张量',
          dictionary: '流量漏洞检测',
          date: '2018-11-17',
          detail: '更换扫描设备'
        }]
      }
    },
    methods: {
      openMsg() {
        this.$message({
          showClose: true,
          message: '添加成功！',
          type: 'success'
        });
        this.dialogAddVisible=false;
      },
      startUse() {
        this.$message({
          showClose: true,
          message: '启用成功！',
          type: 'success'
        });
      },
      onSubmit() {
        console.log('submit!');
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      showAddDialog(){
        console.log(this.dialogAddVisible);
        this.dialogAddVisible = true;
      },
      showDetail(){
        console.log(this.dialogdescripption);
        this.dialogdescripption = true;

      },
      handleCheckedCitiesChange(value) {
        let checkedCount = value.length;
        this.checkAll = checkedCount === this.cities.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length;
      },
      handleClose(done){  //关闭弹窗
        done();
      }
    }
  }
</script>
<style>
  .select{
    position: relative;
    width: 100%;
    margin: 0 auto;
    background:#fff;
    padding: 20px;
    border-radius: 10px;
    overflow: hidden;
  }
  .btndiv{
    width: 100%;
    margin:20px 0px 10px 0px;
    text-align: left;
  }
  .mybutton{
    display: inline-block;
    margin-left:5px;
    margin-right: 5px;
  }
</style>




