<!-- 柱状图 -->
<style lang="stylus" scoped>
  .graph
    height 800px
    //background url('../../assets/bg.jpg') no-repeat
    background-size 100% 100%
    color white
    .main
      width 100%
      height calc(100% - 100px)
      margin-top -15px
</style>

<template>
  <div class="graph">
    <!--<v-header :name="name" :legendArr="legendArr" :myChart="myChart"></v-header>
    <v-filter :myChart="myChart" v-if="myChart._dom"></v-filter>-->
    <div class="main"></div>
  </div>

</template>

<script>
  import echarts from 'echarts'
  import header from '@/views/devices/header'
  import filter from '@/views/devices/filter'

  export default {
    data() {
      return {
        legendArr: [],
        //color: this.$store.state.color,
        color: ['#325B69', '#698570', '#AE5548', '#6D9EA8', '#9CC2B0', '#C98769'],
        myChart: {},
        name: '预测图',
        linevalue: 200,
        graphdata: [],
        now:  new Date(2018, 5, 3),
        oneDay: 24 * 3600 * 1000,
        value: 35,

        option: {
          textStyle: {
            fontSize: 30,
          },
          title: {
            text: '未来一段时间基于当前数据的故障发生率预测分析'
          },
          tooltip: {

            trigger: 'axis',
            formatter: function (params) {
              params = params[0];
              var date = new Date(params.name);
              return date.getDate() + '/' + (date.getMonth() + 1) + '/' + date.getFullYear() + ' : ' + params.value[1];
            },
            axisPointer: {
              animation: false
            }
          },


          xAxis: {
            type: 'time',
            splitLine: {
              show: false
            }
          },
          yAxis: {
            type: 'value',
            boundaryGap: [0, '100%'],
            axisLabel: {
              formatter: '{value} %'
            },
            splitLine: {
              show: false
            },
            itemStyle: {
              normal: {lineStyle: {type: 'solid', color: '#00f'}, label: {show: true, position: 'left'}}
            }
          },
          series: [
            {
              name: '模拟数据',
              type: 'line',
              showSymbol: false,
              hoverAnimation: false,
              data: this.graphdata,

              markLine: {
                data: [
                  {
                    yAxis: 50
                  },
                ]
              }
            }]
        }
      }
    },
    methods: {
      _init() {
        console.log("this is init")
        this.legendArr = this.myChart.getOption().series
        this.legendArr.forEach((data) => {
          data.selected = true;
        })
        this.$root.charts.push(this.myChart)
        window.addEventListener('resize', function() {
          this.myChart.resize()
        }.bind(this))
      },
      randomData() {
       /* console.log("this is randomData");
        console.log('now===='+this.now);*/
        var noww=this.now.getTime()+ this.oneDay;
        // console.log('noww===='+noww)
        this.now = new Date(noww);
        this.value = this.value + Math.random() * 4- 2;
        if (this.value<5)
        {
          this.value=this.value+3;
        }
        if (this.value>60)
        {
          this.value=this.value-5;
        }
        return {
          'name': this.now.toString(),
          'value': [
            [this.now.getFullYear(), this.now.getMonth() + 1, this.now.getDate()].join('/'),
            Math.round(this.value)
          ]
        }
      },
      refreshData(){
        setInterval(()=>{
          for (var i = 0; i <2; i++) {
            this.graphdata.shift();
            this.graphdata.push(this.randomData());
          }
          this.myChart.setOption({
            series: [{
              data: this.graphdata
            }]
          });
        },1500);
      },
      iniGraphData(){

        for (var i = 0; i <50; i++) {

          this.graphdata.push(this.randomData());
        }
      }
    },
    components: {
      'v-header': header,
      'v-filter': filter
    },
    mounted() {
      this.iniGraphData()

      // 基于准备好的dom，初始化echarts实例
      this.myChart = echarts.init(document.querySelector('.graph .main'));
      this.myChart.setOption(this.option);
      this._init();
      this.refreshData();
    }
  }

</script>



