<template>
  <div style="margin-top: 20px;margin-left: 2%;width: 95%" align="center" >
    <strong style="font-family: 黑体;font-size: x-large;text-align: center">用户列表</strong>
    <div class="select">
      <el-form inline="true" size="mini" model="selectItem">
        <el-form-item label="用户名">
          <el-input v-model="selectItem.user" placeholder="请输入用户名"></el-input>
        </el-form-item>
        <el-form-item label="所属水厂">
          <el-select v-model="selectItem.factory" placeholder="请选择水厂">
            <el-option v-for="item in factory_list" :value="item" :key="item">{{item}}</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="职业">
          <el-input v-model="selectItem.position" placeholder="请输入职业"></el-input>
        </el-form-item>
        <el-form-item label="所属部门">
          <el-select v-model="selectItem.depart" placeholder="请选择部门">
            <el-option v-for="item in depart_list" :value="item" :key="item">{{item}}</el-option>
          </el-select>
        </el-form-item>

      </el-form>
      <el-form label-position="center">
        <el-form-item>
          <el-button type="primary" @click="onSubmit">查询</el-button>
        </el-form-item>
        <el-form-item>
          <h3>{{select_callback_info}}</h3>
        </el-form-item>
      </el-form>
      <div class="btndiv">
        <el-dialog title="添加用户" :close-on-press-escape="false" :close-on-click-modal="false" :visible.sync="dialogAddVisible" :before-close="handleClose">
          <el-form :model="form">
            <el-form-item label="昵称：" :label-width="formLabelWidth">
              <el-input v-model="form.nickname" value="45765trfd"></el-input>
            </el-form-item>
            <el-form-item label="实名：" :label-width="formLabelWidth">
              <el-input v-model="form.realname" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="性别：" :label-width="formLabelWidth">
              <el-radio-group v-model="form.content">
                <el-radio label="1">男</el-radio>
                <el-radio label="2">女</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="生日：" :label-width="formLabelWidth">
              <el-date-picker v-model="form.createTime" type="date" placeholder="请选择日期"></el-date-picker>
            </el-form-item>
            <el-form-item label="工厂：" :label-width="formLabelWidth">
              <el-select v-model="form.factory" placeholder="请选择水厂">
                <el-option label="辽宁水厂" value="liaoning"></el-option>
                <el-option label="广东水厂" value="guangdong"></el-option>
                <el-option label="北京水厂" value="beijing"></el-option>
                <el-option label="哈尔滨水厂" value="haerbing"></el-option>
                <el-option label="吉林水厂" value="jilin"></el-option>
                <el-option label="陕西水厂" value="shanxi"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="部门：" :label-width="formLabelWidth">
              <el-select v-model="form.department" placeholder="请选择部门">
                <el-option label="综合管理部" value="zonghe"></el-option>
                <el-option label="财务部" value="caiwu"></el-option>
                <el-option label="安全监察部" value="anquan"></el-option>
                <el-option label="二供服务部" value="ergong"></el-option>
                <el-option label="工程业务部" value="gongcheng"></el-option>
                <el-option label="生产技术部" value="shengchan"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="职位：" :label-width="formLabelWidth">
              <el-input v-model="form.text" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="简介：" :label-width="formLabelWidth">
              <el-input v-model="form.remark" type="textarea" placeholder="300字以内"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click.native="dialogAddVisible = false">取消</el-button>
            <el-button type="primary" @click="openMsg">提交</el-button>
          </div>
        </el-dialog>
        <el-button class="mybutton"  size="large" @click="showAddDialog" icon="el-icon-third-tianjiayonghu"> 添加用户</el-button>
        <el-button class="mybutton" @click="deleteUser" size="large" icon="el-icon-third-delete_member"> 删除选中</el-button>
        <el-button class="mybutton" @click="show3 = !show3" size="large" icon="el-icon-third-role"> 权限分配</el-button>

        <el-collapse-transition  name="slide-fade">
          <div v-show="show3">
            <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange" style="float: left;margin-top: 10px" >
              <el-checkbox v-for="city in cities" :label="city" :key="city">{{city}}</el-checkbox>
            </el-checkbox-group>
            <el-button type="primary" @click="sumbit" style="margin-left: 20px;height: 30px;margin-top: 5px;text-align: center">分配</el-button>
          </div>
        </el-collapse-transition>
        <el-button class="mybutton" @click="refresh_table" icon="el-icon-third-refresh-1-copy"></el-button>
      </div>

      <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange" border="true" height="250">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column label="用户名" prop="user" ></el-table-column>
        <el-table-column prop="name" label="姓名" ></el-table-column>
        <el-table-column prop="sex" label="性别" ></el-table-column>
        <el-table-column prop="birthday" label="出生年月" p></el-table-column>
        <el-table-column prop="factory" label="水厂" ></el-table-column>
        <el-table-column prop="depart" label="部门" ></el-table-column>
        <el-table-column prop="job" label="职位" ></el-table-column>
        <el-table-column prop="address" label="简介" show-overflow-tooltip></el-table-column>
        <el-table-column fixed="right" label="操作" width="120">
          <template slot-scope="scope">
            <el-button @click="showAddDialog" type="text" size="small">编辑</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-col :span="24" class="toolbar">
        <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange"
                       :current-page="currentPage" :page-sizes="[10, 50, 100, 200]" :page-size="10"
                       layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
      </el-col>
    </div>
  </div>
</template>

<script>
  const cityOptions = ['超级管理员', '管理员', '审计员', '普通用户'];
  export default {
    data() {
      return {
        total:2,
        checkedCities: [],
        formLabelWidth: '120px',
        dialogAddVisible:false,
        cities: cityOptions,
        isIndeterminate: true,
        show3:false,
        selectItem:{
          user:'',
          factory:[],
          position:'',
          depart:''
        },
        form: {
          nickname:'',
          realname:'',
          factory: '',
          number: '',
          department: '',
          content: '1',
          remark: '',
          createTime: '',
        },
        factory_list:['辽宁水厂','广东水厂','北京水厂','哈尔滨水厂','吉林水厂','陕西水厂'],
        depart_list:['综合管理部','财务部','安全监察部','二供服务部','工程业务部','生产技术部'],
        tableData: [{
          user: 'dgefd',
          name: '王小虎',
          sex: '男',
          birthday: '1995-02-23',
          factory: '辽宁水厂',
          depart: '综合业务部',
          job: '办事员',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          user: 'xjmie',
          name: '王小二',
          sex: '女',
          birthday: '1995-02-23',
          factory: '辽宁水厂',
          depart: '财务部',
          job: '办事员',
          address: '上海市普陀区金沙江路 1518 弄'
        }]
      }
    },
    methods: {
      openMsg() {
        this.$message({
          showClose: true,
          message: '修改成功！',
          type: 'success'
        });
        this.dialogAddVisible=false;
      },
      onSubmit() {
        console.log('submit!');
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      showAddDialog(){
        console.log(this.dialogAddVisible);
        this.dialogAddVisible = true;
      },
      handleCheckedCitiesChange(value) {
        let checkedCount = value.length;
        this.checkAll = checkedCount === this.cities.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length;
      },
      handleClose(done){  //关闭弹窗
        done();
      }
    }
  }
</script>
<style>
  .select{
    position: relative;
    width: 100%;
    margin: 0 auto;
    background:#fff;
    padding: 20px;
    border-radius: 10px;
    overflow: hidden;
  }
  .btndiv{
    width: 100%;
    margin:20px 0px 10px 0px;
    text-align: left;
  }
  .mybutton{
    display: inline-block;
    margin-left:5px;
    margin-right: 5px;
  }
</style>




