<template>
  <div>
    <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="拼命加载中">
      <el-col :span="24" class="toolbar" style="padding-bottom: 0;">
        <el-form :inline="true" :model="filters">
          <el-form-item>
            <el-input v-model="filters.number" placeholder="请输入设备编号(代码)" auto-complete="off" @keyup.enter.native="handleSearch"></el-input>
          </el-form-item>
          <el-form-item>
            <el-input  placeholder="请输入设备名称" auto-complete="off" @keyup.enter.native="handleSearch"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" size="medium" v-on:click="handleSearch">查询</el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" size="medium" @click="showAddDialog">新增</el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <!--表格数据-->
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column prop="mId" v-if="idShow" label="ID"></el-table-column>
        <el-table-column prop="mNumber" label="设备编号" width="180" sortable></el-table-column>
        <el-table-column prop="mType" label="设备名称" width="180"></el-table-column>
        <el-table-column prop="mContent" label="设备状态" show-overflow-tooltip></el-table-column>
        <el-table-column prop="eRemark" label="备注" width="90"></el-table-column>
        <el-table-column prop="createTime" label="创建日期" width="180"></el-table-column>
        <el-table-column prop="updateTime" label="更新日期" width="180"></el-table-column>
        <el-table-column prop="recordStatus" label="状态" width="180"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="handleDetail(scope.$index, scope.row)">详情</el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-col :span="24" class="toolbar">
        <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange"
                       :current-page="currentPage" :page-sizes="[10, 50, 100, 200]" :page-size="10"
                       layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
      </el-col>
    </el-col>

    <el-dialog title="设备添加" :close-on-press-escape="false" :close-on-click-modal="false" :visible.sync="dialogAddVisible" :before-close="handleClose">
      <el-form :model="form">
        <el-form-item label="设备编号(代码)：" :label-width="formLabelWidth">
          <el-input v-model="form.number" placeholder="请输入设备编号(代码)" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="设备名称：" :label-width="formLabelWidth">
          <el-select v-model="form.type" placeholder="请选择设备名称">
            <el-option label="RTU1" value="receive"></el-option>
            <el-option label="RTU2" value="forward"></el-option>
            <el-option label="RTU3" value="receive"></el-option>
            <el-option label="PLC1" value="forward"></el-option>
            <el-option label="PLC2" value="receive"></el-option>
            <el-option label="PLC3" value="forward"></el-option>
            <el-option label="DSC1" value="receive"></el-option>
            <el-option label="DSC2" value="forward"></el-option>
            <el-option label="DSC3" value="receive"></el-option>
            <el-option label="Camer1" value="forward"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="设备状态：" :label-width="formLabelWidth">
          <el-radio-group v-model="form.content">
            <el-radio label="在线">在线</el-radio>
            <el-radio label="离线">离线</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="设备备注：" :label-width="formLabelWidth">
          <el-input v-model="form.remark" type="textarea" placeholder="请填写设备备注"></el-input>
        </el-form-item>
        <el-form-item label="创建日期：" :label-width="formLabelWidth">
          <el-date-picker v-model="form.createTime" type="date" placeholder="请选择日期"></el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="dialogAddVisible = false">取消</el-button>
        <el-button type="primary" @click="openMsg">提交</el-button>
      </div>
    </el-dialog>

  </div>
</template>
<script>
  export default {
    name: 'deviceopration',
    data: function(){
      return {
        loading: false,
        radio: '1',
        idShow: false,
        keyword: "集团",
        total:6,
        currentPage: 1,
        pageSize: 10,
        tableData: [{
          mId: 1,
          mNumber: '10001',
          mType: 'PLC1',
          mContent: '在线',
          eRemark: '正常',
          createTime: '2018-11-17',
          updateTime: '2018-11-17',
          recordStatus: '状态良好'
        },
          {
            mId: 2,
            mNumber: '10002',
            mType: 'PLC2',
            mContent: '离线',
            eRemark: '正常',
            createTime: '2018-11-17',
            updateTime: '2018-11-18',
            recordStatus: '状态良好'
          },{
            mId: 3,
            mNumber: '10003',
            mType: 'PLC3',
            mContent: '离线',
            eRemark: '正常',
            createTime: '2018-11-17',
            updateTime: '2018-11-18',
            recordStatus: '状态良好'
          },{
            mId: 4,
            mNumber: '10004',
            mType: 'DSC1',
            mContent: '离线',
            eRemark: '正常',
            createTime: '2018-11-17',
            updateTime: '2018-11-18',
            recordStatus: '状态异常'
          },{
            mId: 5,
            mNumber: '10005',
            mType: 'DSC2',
            mContent: '离线',
            eRemark: '正常',
            createTime: '2018-11-19',
            updateTime: '2018-11-19',
            recordStatus: '状态异常'
          },{
            mId: 6,
            mNumber: '10006',
            mType: 'Camer1',
            mContent: '在线',
            eRemark: '正常',
            createTime: '2018-11-18',
            updateTime: '2018-11-20',
            recordStatus: '状态良好'
          }],
        form: {
          number: '',
          type: '',
          content: '1',
          remark: '',
          createTime: '',
        },
        formLabelWidth: '120px',
        dialogAddVisible: false,
        filters: {
          number: ''
        }
      };
    },
    methods: {
      handleSearch(){
        console.info(this.filters.number);
      },
      handleDetail(index, row) {
        console.log(index, row);
        console.log(row.mId);
      },
      handleDelete(index, row) {
        console.log(index, row);
      },
      handleSizeChange(val){
//        this.pageSize = val;
//        this.currentPage = 1;
      },
      handleCurrentChange(val){
//        this.currentPage = val;
      },
      showAddDialog(){
        this.dialogAddVisible = true;
      },
      handleClose(done){  //关闭弹窗
        done();
      },
      openMsg() {
        this.$message({
          showClose: true,
          message: '添加成功',
          type: 'success'
        })
        this.dialogAddVisible = false;

      }
    }
  }
</script>



