<!-- 柱状图 -->
<style lang="stylus" scoped>
  .radar
    height 800px
    //background url('../../assets/bg.jpg') no-repeat
    background-size 100% 100%
    color white
    .main
      width 100%
      height calc(100% - 100px)
      margin-top -15px
</style>

<template>
  <div class="radar">
    <!--<v-header :name="name" :legendArr="legendArr" :myChart="myChart"></v-header>
    <v-filter :myChart="myChart" v-if="myChart._dom"></v-filter>-->
    <div class="main"></div>
  </div>

</template>

<script>
  import echarts from 'echarts'
  import header from '@/views/devices/header'
  import filter from '@/views/devices/filter'

  export default {
    data() {
      return {
        legendArr: [],
        //color: this.$store.state.color,
        color: ['#325B69', '#698570', '#AE5548', '#6D9EA8', '#9CC2B0', '#C98769'],
        myChart: {},
        name: '柱状图',






        lineStyle : {
          normal: {
            width: 1,
            opacity: 0.5
          }
        },

        option : {
      //    backgroundColor: '#101736',
          color: ['#325B69', '#698570', '#AE5548', '#6D9EA8', '#9CC2B0'],
          title: {
            text: '系统整体评价',
            left: 'left',
            top: 20,
            textStyle: {
              color: '#000000'
            }
          },
          legend: {
            show: true,
            // icon: 'circle',//图例形状

            bottom:0,
            center: 0,
            itemWidth: 14, // 图例标记的图形宽度。[ default: 25 ]
            itemHeight: 14, // 图例标记的图形高度。[ default: 14 ]
            itemGap: 21, // 图例每项之间的间隔。[ default: 10 ]横向布局时为水平间隔，纵向布局时为纵向间隔。
            textStyle: {
              fontSize: 20,
              color: '#ade3ff'
            },
            data: ['厂1', '厂2', '厂3','厂4','厂5'],
          },
          radar: [{

            indicator: [
              {
                text: '保密性',
                max: 100
              },
              {
                text: '可信性',
                max: 100
              },
              {
                text: '可靠性',
                max: 100
              },
              {
                text: '不可否认性',
                max: 100
              },
              {
                text: '可用性',
                max: 100
              },
              {
                text: '完整性',
                max: 100
              }
            ],

            textStyle: {
              color: 'red'
            },
            center: ['50%', '50%'],
            radius:  '75%',
            startAngle: 90,
            splitNumber: 3,
            orient: 'horizontal', // 图例列表的布局朝向,默认'horizontal'为横向,'vertical'为纵向.
            // shape: 'circle',
            // backgroundColor: {
            //     image:imgPath[0]
            // },
            name: {
              formatter: '{value}',
              textStyle: {
                fontSize: 20, //外圈标签字体大小
                color: '#150008' //外圈标签字体颜色
              }
            },
            splitArea: { // 坐标轴在 grid 区域中的分隔区域，默认不显示。
              show: false,
              areaStyle: { // 分隔区域的样式设置。
                color: ['#a9a9a9', '#A9A9A9'], // 分隔区域颜色。分隔区域会按数组中颜色的顺序依次循环设置颜色。默认是一个深浅的间隔色。
              }
            },
            // axisLabel:{//展示刻度
            //     show: true
            // },
            axisLine: { //指向外圈文本的分隔线样式
              lineStyle: {
                color: '#627c94'
              }
            },
            splitLine: {
              lineStyle: {
                color: '#f0fcff', // 分隔线颜色
                width: 1, // 分隔线线宽
              }
            }
          }, ],
          series: [{
            name: '雷达图',
            type: 'radar',
            itemStyle: {
              emphasis: {
                lineStyle: {
                  width: 8
                }
              }
            },
            data: [
              {
                name: '厂1',
                value: [55, 65, 75, 90, 22, 55],
                areaStyle: {
                  normal: { // 单项区域填充样式
                    color: {
                      type: 'linear',
                      x: 0, //右
                      y: 0, //下
                      x2: 1, //左
                      y2: 1, //上
                      colorStops: [{
                        offset: 0,
                        color: '#325B69'/***********/
                      }, {
                        offset: 0.5,
                        color: '#325B69'
                      }, {
                        offset: 1,
                        color: '#325B69'/*****************/
                      }],
                      globalCoord: false
                    },
                    opacity: 0.6// 区域透明度
                  }
                },
                symbolSize: 4.5, // 单个数据标记的大小，可以设置成诸如 10 这样单一的数字，也可以用数组分开表示宽和高，例如 [20, 10] 表示标记宽为20，高为10。
                label: {                    // 单个拐点文本的样式设置
                  normal: {
                    show: false,             // 单个拐点文本的样式设置。[ default: false ]
                    position: 'top',        // 标签的位置。[ default: top ]
                    distance: 2,            // 距离图形元素的距离。当 position 为字符描述值（如 'top'、'insideRight'）时候有效。[ default: 5 ]
                    color: '#150008',          // 文字的颜色。如果设置为 'auto'，则为视觉映射得到的颜色，如系列色。[ default: "#fff" ]
                    fontSize: 14,           // 文字的字体大小
                    formatter:function(params) {
                      return params.value;
                    }
                  }
                },
                itemStyle: {
                  normal: { //图形悬浮效果
                    borderColor: '#325B69',
                    borderWidth: 4.5
                  }
                },
                // lineStyle: {
                //     normal: {
                //         opacity: 0.5// 图形透明度
                //     }
                // }
              },
              {
                name: '厂2',
                value: [50, 20, 45, 30, 75, 32],
                symbolSize: 4.5,
                itemStyle: {
                  normal: {
                    borderColor: '#698570',
                    borderWidth: 4.5,
                  }
                },
                areaStyle: {
                  normal: { // 单项区域填充样式
                    color: {
                      type: 'linear',
                      x: 0, //右
                      y: 0, //下
                      x2: 1, //左
                      y2: 1, //上
                      colorStops: [{
                        offset: 0,
                        color: '#698570'
                      }, {
                        offset: 0.5,
                        color: '#698570'
                      }, {
                        offset: 1,
                        color: '#698570'
                      }],
                      globalCoord: false
                    },
                    opacity: 0.6 // 区域透明度
                  }
                },
                // lineStyle: {
                //     normal: {
                //         opacity: 0.5// 图形透明度
                //     }
                // }
              },
              {
                name: '厂3',
                value: [37, 80, 12, 50, 25, 43],
                symbolSize: 4.5,
                itemStyle: {
                  normal: {
                    borderColor: '#AE5548',
                    borderWidth: 4.5,
                  }
                },
                areaStyle: {
                  normal: { // 单项区域填充样式
                    color: {
                      type: 'linear',
                      x: 0, //右
                      y: 0, //下
                      x2: 1, //左
                      y2: 1, //上
                      colorStops: [{
                        offset: 0,
                        color: '#AE5548'
                      }, {
                        offset: 0.5,
                        color: '#AE5548'
                      }, {
                        offset: 1,
                        color: '#AE5548'
                      }],
                      globalCoord: false
                    },
                    opacity: 0.6 // 区域透明度
                  }
                }
              }
              ,{
                name: '厂4',
                value: [50, 20, 45, 70, 85, 90],
                symbolSize: 4.5,
                itemStyle: {
                  normal: {
                    borderColor: '#6D9EA8',
                    borderWidth: 4.5,
                  }
                },
                areaStyle: {
                  normal: { // 单项区域填充样式
                    color: {
                      type: 'linear',
                      x: 0, //右
                      y: 0, //下
                      x2: 1, //左
                      y2: 1, //上
                      colorStops: [{
                        offset: 0,
                        color: '#6D9EA8'
                      }, {
                        offset: 0.5,
                        color: '#6D9EA8'
                      }, {
                        offset: 1,
                        color: '#6D9EA8'
                      }],
                      globalCoord: false
                    },
                    opacity: 0.6 // 区域透明度
                  }
                },
                // lineStyle: {
                //     normal: {
                //         opacity: 0.5// 图形透明度
                //     }
                // }
              },
              {
                name: '厂5',
                value: [50, 20, 45, 30, 66, 51],
                symbolSize: 4.5,
                itemStyle: {
                  normal: {
                    borderColor: '#9CC2B0',
                    borderWidth: 2.5,
                  }
                },
                areaStyle: {
                  normal: { // 单项区域填充样式
                    color: {
                      type: 'linear',
                      x: 0, //右
                      y: 0, //下
                      x2: 1, //左
                      y2: 1, //上
                      colorStops: [{
                        offset: 0,
                        color: '#9CC2B0'
                      }, {
                        offset: 0.5,
                        color: '#9CC2B0'
                      }, {
                        offset: 1,
                        color: '#9CC2B0'
                      }],
                      globalCoord: false
                    },
                    opacity: 0.5 // 区域透明度
                  }
                },
                // lineStyle: {
                //     normal: {
                //         opacity: 0.5// 图形透明度
                //     }
                // }
              },]
          }, ]
        }
      }
    },
    methods: {
      _init() {
        this.legendArr = this.myChart.getOption().series
        this.legendArr.forEach((data) => {
          data.selected = true;
        })
        this.$root.charts.push(this.myChart)
        window.addEventListener('resize', function() {
          this.myChart.resize()
        }.bind(this))
      }
    },
    components: {
      'v-header': header,
      'v-filter': filter
    },
    mounted() {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = echarts.init(document.querySelector('.radar .main'));
      this.myChart.setOption(this.option);
      this._init()
    }
  }

</script>



