<!-- 柱状图 -->
<style lang="stylus" scoped>
  .cvesort
    height 800px
    //background url('../../assets/bg.jpg') no-repeat
    background-size 100% 100%
    color white
    .main
      width 100%
      height calc(100% - 100px)
      margin-top -15px
</style>

<template>
  <div class="cvesort">
    <!--<v-header :name="name" :legendArr="legendArr" :myChart="myChart"></v-header>-->
    <!--<v-filter :myChart="myChart" v-if="myChart._dom"></v-filter>-->
    <div class="main"></div>
  </div>

</template>

<script>
  import echarts from 'echarts'
  import header from '@/views/devices/header'
  import filter from '@/views/devices/filter'

  export default {
    data() {
      return {
        legendArr: [],
        //color: this.$store.state.color,
        color: ['#325B69', '#698570', '#AE5548', '#6D9EA8', '#9CC2B0', '#C98769'],
        myChart: {},
        option :{
          title: {
            text: '出现次数最多漏洞排行',
            //subtext: '纯属虚构'
          },
          tooltip: {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c}%"
          },
          toolbox: {
            feature: {
              dataView: {readOnly: false},
              restore: {},
              saveAsImage: {}
            }
          },
          legend: {
            padding: 30,
            data: ['CVE-2018-15387','OSVDB 8179','CVE-2018-8120','CNNVD-201606-433','CNVD-2014-0282']
          },
          calculable: true,
          series: [
            {
              name:'漏斗图',
              type:'funnel',
              left: '15%',
              top: 70,
              //x2: 80,
              bottom: 40,
              width: '70%', height: '85%',

              // height: {totalHeight} - y - y2,
              min: 0,
              max: 100,
              minSize: '0%',
              maxSize: '100%',
              sort: 'descending',
              gap: 2,
              label: {
                normal: {
                  show: true,
                  position: 'inside'
                },
                emphasis: {
                  textStyle: {
                    fontSize: 20
                  }
                }
              },
              labelLine: {
                normal: {
                  length: 10,
                  lineStyle: {
                    width: 1,
                    type: 'solid'
                  }
                }
              },
              itemStyle: {
                normal: {
                  borderColor: '#fff',
                  borderWidth: 1
                }
              },
              data: [
                {value: 60, name: 'CVE-2018-8120'},
                {value: 40, name: 'CNVD-2014-0282'},
                {value: 20, name: 'CVE-2018-15387 '},
                {value: 80, name: 'OSVDB 8179'},
                {value: 100, name: 'CNNVD-201606-433'}
                // SolarWinds Virtualization Manager 安全漏洞
              ]
            }
          ]
        }

      }
    },
    methods: {
      _init() {
        this.legendArr = this.myChart.getOption().series
        this.legendArr.forEach((data) => {
          data.selected = true;
        })
        this.$root.charts.push(this.myChart)
        window.addEventListener('resize', function() {
          this.myChart.resize()
        }.bind(this))
      }
    },
    components: {
      'v-header': header,
      'v-filter': filter
    },
    mounted() {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = echarts.init(document.querySelector('.cvesort .main'));
      this.myChart.setOption(this.option);
      this._init()
    }
  }

</script>



